@import url(https://fonts.googleapis.com/css?family=Buenard:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::selection {
  color: white;
  background: pink;
}


* {
  font-family: 'Buenard', serif;
  #background-color: #fff0f2;
}

.App {
  width: 110vh;
  max-width: 100%;
  #border: 1px solid lightgray;
  margin-left: auto;
  margin-right: auto;
}


#line {
  border-top: 1px solid lightgray;
  width: 110vh;
  max-width: 100%;
  margin-top: 40px;
  margin-bottom: 40px;
}

#head {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

#paral {
  height: 50vh;
  width: 110vh;
  margin-left: auto;
  margin-right: auto;
  background: pink;

}

.Article:hover {
  cursor: pointer;
}
.Article .headline a {
  color: black;
  text-decoration: none;
}
.Article .headline a:hover {
  color: darkblue;
  text-decoration: underline;
}

.Article .headline h4 {
  color: gray;
}

button {
  margin: 0;
  background: none;
  border: none;
}



.Head {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.Head .Article {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.Head .img {
  border: 1px solid;
  height: 39vh;
  width: 56vh;
}

#description {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  font-size: 14px;
  text-align: justify;
  border-radius: 1px;
}

.Head .headline {
  margin: 5px;
  font-size: 120%;
  margin-right: 10px;
}

.Featured {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

#Featured {
  display: flex;
  flex-direction: row;
}

.Featured .Article {
  display: flex;
  flex-direction: row;
  padding: 15px;
}

.Featured .headline {
  margin: 5px;
}

.Featured .img {
  height: 80px;
  width: 100px;
  border: 1px solid;
}

.Featured h3 {
  margin: 0;
  font-size: 15px;
}

.Featured h4 {
  margin: 0;
  font-size: 11px;
}



.Latest {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
}

.Latest .Article {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.Latest .img {
  width: 25vh;
  height: 18vh;
  border: 1px solid;
  margin-left: 15px;
  margin-right: 15px;
}

.Latest h3 {
  margin: 0;
}
.Latest h4 {
  margin: 0;
}

.Multimedia {
  max-width: 110vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.Multimedia #img_container {
  height: 40vh;
  width: 60vh;
  border: 1px solid;
}

.Multimedia #img_description {
  width: 25vh;
}
.Multimedia #img_description * {
  margin: 3px;
}

.Multimedia h2 {
  margin: 0;
}

.About {
  max-width: 80vh;
  margin-top: 30px;
  margin-bottom: 30px;
  border: 1px solid gray;
  margin-left: auto;
  margin-right: auto;
}

.About #meat {
  padding: 20px;
}

